.header {
    padding: 25px 0 45px;
    background-color: var(--white);
    &__container {
        position: relative;
    }

    &__item {
        display: flex;
        align-items: center;
        &:not(:last-child) {
            margin-right: 35px;
        }
        &-link {
            transition: opacity .3s ease-in-out;
            &:hover {
                opacity: .5;
            }

            &:active {
                opacity: 1;
            }
        }
    }

    &__feedback {
        max-width: 330px;
        width: 100%;
        &-link {
            width: 100%;
        }
    }

    &__burger {
        width: 25px;
        height: 15px;
        position: relative;
        display: flex;
        z-index: 3;

        &::before,
        &::after {
            content: "";
            position: absolute;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: var(--black);
            transition: transform .3s ease-in-out, top .3s ease-in-out;
        }

        &::before {
            top: 0;
        }

        &::after {
            top: calc(100% - 1px);
        }

        &-line {
            position: absolute;
            left: 0;
            top: 50%;
            width: 100%;
            height: 1px;
            background-color: var(--black);
            transform: translateY(-50%);
            transition: transform .3s ease-in-out
        }
    }

    &__burger.active &__burger-line {
        transform: scale(0);
    }

    &__burger.active::before {
        transform: rotate(45deg);
        top: 50%;
    }
    &__burger.active::after {
        transform: rotate(-45deg);
        top: 50%;
    }
}

@media screen and (max-width: 991px) {
    .header {
        padding: 20px 0;
        z-index: 3;
        
        &__svg {
            visibility: hidden;
            opacity: 0;
            z-index: 0;
            transition: opacity .3s ease-in-out, visibility .3s ease-in-out, z-index .3s ease-in-out;
        }

        &__svg.active {
            visibility: visible;
            opacity: 1;
            z-index: 3;
        }
        &__menu {
            position: fixed;
            padding: 150px 0px 20px 0px;
            margin-top: 50px;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: var(--white);
            visibility: hidden;
            opacity: 0;
            transform: translateY(-100vh);
            overflow-y: auto;
            transition: visibility .3s ease-in-out, opacity .3s ease-in-out, transform .3s ease-in-out;
        }

        &__menu.active {
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
        }

        &__nav {
            overflow-y: auto;
        }

        &__item {
            position: relative;
            &::before {
                content: "";
                width: 100%;
                height: 1px;
                background-color: var(--black);
                position: absolute;
                top: calc(50% - 2px);
                left: 0;
                opacity: 0;
                transition: opacity .3s ease-in-out;
            }
            &:not(:last-child) {
                margin-right: 0px;
                margin-bottom: 20px;
            }

            &-link {
                padding: 0 20px;
                background-color: var(--white);
                transition: transform .3s ease-in-out;
                &:hover {
                    opacity: 1;
                }
            }

        }

        &__item.active::before {
            opacity: 1;
        }

    }
}

@media screen and (max-width: 639px) {
    .header {
        padding: 20px 0 17px;
        &__svg svg {
            width: 39.16px;
            height: 13px;
        }

        &__burger {
            width: 22px;
            height: 13.2px;
        }

        &__item-link {
            padding: 0 8px;
        }
    }

    
}